'use client';
import { useEffect, useRef, useState } from 'react';
import { animate, scroll, spring } from 'motion';
import { ReactLenis } from 'lenis/react';
import { TextAnimate } from '@/components/ui/text-animate';
import { TypingAnimation } from "@/components/ui/typing-animation";
import { Meteors } from "@/components/ui/meteors";
import { useRouter } from 'next/navigation';

export default function HorizontalScroll() {
  const ulRef = useRef();
  const [isMobile, setIsMobile] = useState(false);
  const navigate = useRouter();

  useEffect(() => {
    const checkScreenSize = () => setIsMobile(window.innerWidth < 768);
    checkScreenSize(); 
    window.addEventListener('resize', checkScreenSize);
    return () => window.removeEventListener('resize', checkScreenSize);
  }, []);

  useEffect(() => {
    if (!ulRef.current || isMobile) return; // Skip animation on mobile

    const items = ulRef.current.children;
    const section = document.querySelector('section');

    scroll(
      animate(ulRef.current, { x: ['0%', `-${(items.length - 1) * 100}%`] }, { easing: spring() }),
      { target: section }
    );
  }, [isMobile]);

  return (
    <ReactLenis root>
      <main>
        <article>
          {/* HEADER SECTION */}
          <header className="text-white relative w-full bg-slate-950 grid place-content-center min-h-screen">
            <Meteors number={50} />
            <div className="absolute inset-0 bg-[linear-gradient(to_right,#4f4f4f2e_1px,transparent_1px),linear-gradient(to_bottom,#4f4f4f2e_1px,transparent_1px)] bg-[size:14px_24px]"></div>
            <h1 className="text-3xl sm:text-5xl font-bold text-center tracking-tight px-4">
              <TypingAnimation>CodeQuest: Your Ultimate Coding Adventure</TypingAnimation>
            </h1> 
          </header>

          {/* SCROLLABLE SECTIONS */}
          <section className={`relative w-full ${isMobile ? 'overflow-y-auto' : 'overflow-x-hidden'}`}>
            <ul ref={ulRef} className={`flex ${isMobile ? 'flex-col' : 'flex-row'} w-full md:w-max snap-mandatory`}>
              {[
                { title: "LEARN", color: "from-green-400 to-blue-500", text: "Begin your journey with challenges that teach foundational concepts." },
                { title: "SOLVE", color: "from-purple-400 to-pink-500", text: "Unlock new coding problems as you solve each challenge." },
                { title: "CONNECT", color: "from-yellow-400 to-orange-500", text: "Join a community of coders and grow together." },
                { title: "ACHIEVE", color: "from-teal-400 to-cyan-500", text: "Track your progress and celebrate milestones." }
              ].map(({ title, color, text }, index) => (
                <li key={index} 
                  className={`snap-center min-h-screen w-screen md:w-[100vw] flex flex-col justify-center items-center bg-gradient-to-r ${color} px-6 text-center`}
                >
                  <h2 className="text-[8vw] md:text-5xl font-semibold text-white">
                    <span className="text-black">{title}</span>
                  </h2>
                  <p className="text-lg md:text-xl mt-4 text-white max-w-lg">{text}</p>
                </li>
              ))}

              {/* FINAL SECTION */}
              <li className="snap-center min-h-screen w-screen bg-gradient-to-r from-gray-800 to-gray-900 flex flex-col justify-center items-center px-6 text-center">
                <h2 className="text-[8vw] md:text-5xl font-semibold text-white">
                  <span className="text-blue-400">
                    <TextAnimate animation="blurInUp" by="character">WELCOME TO CODEQUEST</TextAnimate>
                  </span>
                </h2>
                <p className="text-lg md:text-xl mt-4 text-white max-w-lg">
                  <TextAnimate animation="blurInUp" by="character">
                    Your journey has just begun. Explore, learn, and grow with CodeQuest.
                  </TextAnimate>
                </p>
                <button className="mt-6 px-4 sm:px-6 py-3 bg-blue-600 text-white text-base sm:text-lg rounded-lg shadow-lg hover:bg-blue-700" onClick={() => navigate.push('/problemset')}>
                  Get Started Now
                </button>
              </li>
            </ul>
          </section>
        </article>

        {/* PROGRESS BAR */}
        <div className="progress fixed left-0 right-0 h-2 rounded-full bg-[#0c0c29] bottom-[50px] scale-x-0"></div>
      </main>
    </ReactLenis>
  );
}
